import React from 'react';
import ReactFlow, { MiniMap, Controls, Background, useNodesState, useEdgesState, addEdge } from 'react-flow-renderer';

const Diagramma = ({ servizi, onRemoveNode }) => {
  const [nodes, setNodes, onNodesChange] = useNodesState([]);
  const [edges, setEdges, onEdgesChange] = useEdgesState([]);

  const onConnect = (params) => setEdges((eds) => addEdge(params, eds));

  const addNode = (servizio) => {
    const newNode = {
      id: servizio.nome,
      data: { label: servizio.nome },
      position: { x: Math.random() * 400, y: Math.random() * 400 },
    };
    setNodes((nds) => nds.concat(newNode));
  };

  const removeNode = (id) => {
    setNodes((nds) => nds.filter((node) => node.id !== id));
    setEdges((eds) => eds.filter((edge) => edge.source !== id && edge.target !== id));
    if (onRemoveNode) onRemoveNode(id);
  };

  return (
    <div style={{ height: '70vh', position: 'relative' }}>
      <ReactFlow
        nodes={nodes}
        edges={edges}
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        onConnect={onConnect}
        fitView
      >
        <MiniMap />
        <Controls />
        <Background />
      </ReactFlow>
      <button onClick={() => addNode(servizi[0])} style={{ position: 'absolute', top: 10, left: 10 }}>
        Aggiungi Servizio
      </button>
      {nodes.map((node) => (
        <button
          key={node.id}
          onClick={() => removeNode(node.id)}
          style={{ position: 'absolute', top: node.position.y + 40, left: node.position.x }}
        >
          Rimuovi {node.data.label}
        </button>
      ))}
    </div>
  );
};

export default Diagramma;