import React, { useEffect, useState } from 'react';
import serviziData from './servizi.json';
import Diagramma from './Diagramma';

const App = () => {
  const [servizi, setServizi] = useState([]);
  const [selectedServices, setSelectedServices] = useState([]);
  const [requisitiSelezionati, setRequisitiSelezionati] = useState({});

  useEffect(() => {
    setServizi(serviziData);
  }, []);

  const handleSelectService = (serviceName) => {
    setSelectedServices((prev) =>
      prev.includes(serviceName)
        ? prev.filter((name) => name !== serviceName)
        : [...prev, serviceName]
    );
  };

  const handleSelectRequisito = (serviceName, requisito) => {
    setRequisitiSelezionati((prev) => {
      const currentRequisiti = prev[serviceName] || [];
      if (currentRequisiti.includes(requisito)) {
        return {
          ...prev,
          [serviceName]: currentRequisiti.filter((r) => r !== requisito),
        };
      } else {
        return {
          ...prev,
          [serviceName]: [...currentRequisiti, requisito],
        };
      }
    });
  };

  const handleRemoveNode = (id) => {
    setSelectedServices((prev) => prev.filter((service) => service !== id));
    setRequisitiSelezionati((prev) => {
      const newRequisiti = { ...prev };
      delete newRequisiti[id];
      return newRequisiti;
    });
  };

  return (
    <div>
      <h1>Lista di Servizi Disponibili</h1>
      <ul className="service-list">
        {servizi.map((servizio) => (
          <li key={servizio.nome} className="service-item">
            <h2>{servizio.nome}</h2>
            <p>{servizio.descrizione}</p>
            <button onClick={() => handleSelectService(servizio.nome)}>
              {selectedServices.includes(servizio.nome) ? 'Deseleziona' : 'Seleziona'}
            </button>
            <div className="requisiti">
              <h3>Requisiti:</h3>
              <ul>
                {servizio.requisiti.map((requisito) => (
                  <li key={requisito}>
                    <label>
                      <input
                        type="checkbox"
                        checked={requisitiSelezionati[servizio.nome]?.includes(requisito) || false}
                        onChange={() => handleSelectRequisito(servizio.nome, requisito)}
                      />
                      {requisito}
                    </label>
                  </li>
                ))}
              </ul>
            </div>
          </li>
        ))}
      </ul>
      <h2>Servizi Selezionati:</h2>
      <ul>
        {selectedServices.map((name, index) => (
          <li key={index}>
            {name} - Requisiti selezionati: {requisitiSelezionati[name]?.join(', ') || 'Nessuno'}
          </li>
        ))}
      </ul>
      <Diagramma servizi={servizi} onRemoveNode={handleRemoveNode} />
    </div>
  );
};

export default App;